*, *::after, *::before {
    box-sizing: border-box;
}

body {
    min-width: 280px;
    font-family: 'Inter', sans-serif;
}
@media (max-width: 1440px) {
    body {
        padding: 0 30px;
    }
}

.container {
    max-width: 1470px;
    margin: 0 auto;
}

img,
svg {
    width: 100%;
    height: 100%;
}

button,
input {
    font-family: 'Inter', sans-serif;
    background: none;
    outline: none;
    border: none;
}

